import { S1Application } from './s1-application.model';
import { PaymentMethod } from './payment-method.model';
import { MultipleApplication } from './multiple-application.model';
import { S2Application } from './s2-application.model';

export class Payment {
    id: number;
    amountDue: number;
    amountReceive: number;
    appFormReference: string;
    applicationForm: S1Application;
    basicRate: number;
    discount: number;
    filename: string;
    officialReceiptNo: string;
    paymentAttachmentReference: string;
    paymentDate: Date;
    paymentMethod: PaymentMethod = new PaymentMethod();
    paymentStatus: string; // PAID, UNPAID
    remarks: string;
    status: string;
    vat: number;
    withholdingTax: number;
    amountToSettle: number;
    companyName: string;
    companyId: number;

    multipleApplication: MultipleApplication;
    filedBirForm: boolean = false;
    applicationFormS2: S2Application;

    constructor() {
        this.filedBirForm = false;
        this.remarks = '';
    }
}
